.countdown {
  margin-top: 75px;
  padding: 20px 0px;
  text-align: center !important;
}

.num {
  display: inline-grid;
  text-align: center;
  font-family: 'Poppins';
  font-size: 36px;
  z-index: 10;
  position: relative;
  overflow:hidden;
  letter-spacing: 3px;
  max-width: 55px;
  min-width: 55px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #374146;
}

.hr {
  left: -82px;
  top: 60px;
}

.min {
  left: -105px;
  top: 5px;
}

.sec {
  left: -86px;
  top: 25px;
}

.countdown h2 {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 40px;
  font-family: 'Poppins';
}

.rightgear, .leftgear, .middlegear {
  vertical-align: middle;
  position: relative;
}

.countdown .middlegear {
  height: 280px;
  top: -20px;
  margin: auto -60px;
  -webkit-animation: spinbackward 1s infinite;
          animation: spinbackward 1s infinite;
}

.countdown .leftgear {
  margin: auto -70px;
  top: 35px;
  height: 250px;
  -webkit-animation: spinforward 1s infinite;
          animation: spinforward 1s infinite;
}

.countdown .rightgear {
  margin: auto -65px;
  height: 250px;
  -webkit-animation: spinforward 1s infinite;
          animation: spinforward 1s infinite;
}

@-webkit-keyframes spinforward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
}

@keyframes spinforward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
}

@-webkit-keyframes spinbackward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
}

@keyframes spinbackward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
}



