@import url('https://fonts.googleapis.com/css?family=Hind+Siliguri|Poppins');

* {
  margin: 0;
  padding: 0;
}

body, html {
  min-height: 100%;
  font-family: 'Hind Siliguri', sans-serif;
}

html {
  background: -webkit-gradient(linear, left top, left bottom, from(#F1FDFF), to(#C5F4FF));
  background: -webkit-linear-gradient(top, #F1FDFF, #C5F4FF);
  background: -o-linear-gradient(top, #F1FDFF, #C5F4FF);
  background: linear-gradient(to bottom, #F1FDFF, #C5F4FF);
}



